'use client'
import { useState, useEffect, useRef } from 'react'

function BulletproofMap() {
  const [isClient, setIsClient] = useState(false)
  const [mapLoaded, setMapLoaded] = useState(false)
  const [error, setError] = useState<string | null>(null)
  const [loadingStep, setLoadingStep] = useState('Initializing...')
  const mapRef = useRef<any>(null)
  const containerRef = useRef<HTMLDivElement>(null)

  useEffect(() => {
    setIsClient(true)
    
    const loadMap = async () => {
      try {
        setLoadingStep('Loading CSS...')
        
        // Load Leaflet CSS
        if (!document.querySelector('link[href*="leaflet.css"]')) {
          const cssLink = document.createElement('link')
          cssLink.rel = 'stylesheet'
          cssLink.href = 'https://unpkg.com/leaflet@1.9.4/dist/leaflet.css'
          cssLink.integrity = 'sha256-p4NxAoJBhIIN+hmNHrzRCf9tD/miZyoHS5obTRR9BMY='
          cssLink.crossOrigin = ''
          document.head.appendChild(cssLink)
          
          // Wait for CSS to load
          await new Promise((resolve) => {
            cssLink.onload = resolve
            setTimeout(resolve, 1000) // Fallback timeout
          })
        }

        setLoadingStep('Loading Leaflet library...')
        
        // Load Leaflet JS
        const L = await import('leaflet')
        
        setLoadingStep('Creating map...')
        
        // Make sure container exists and is empty
        const container = containerRef.current
        if (!container) {
          throw new Error('Map container not found')
        }
        
        // Clear any existing map
        if (mapRef.current) {
          mapRef.current.remove()
        }
        container.innerHTML = ''
        
        // Create map
        const map = L.map(container, {
          center: [51.505, -0.09],
          zoom: 13,
          zoomControl: true
        })
        
        mapRef.current = map
        
        setLoadingStep('Loading map tiles...')
        
        // Add tile layer
        const tileLayer = L.tileLayer('https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png', {
          attribution: '¬© <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors',
          maxZoom: 19
        })
        
        tileLayer.addTo(map)
        
        // Add a marker
        L.marker([51.505, -0.09])
          .addTo(map)
          .bindPopup('Hello! Your map is working! üó∫Ô∏è')
          .openPopup()
        
        setLoadingStep('Map ready!')
        setMapLoaded(true)
        console.log('‚úÖ Map loaded successfully!')
        
      } catch (err) {
        console.error('‚ùå Map loading failed:', err)
        setError(err instanceof Error ? err.message : 'Unknown error occurred')
      }
    }

    if (isClient) {
      loadMap()
    }

    // Cleanup function
    return () => {
      if (mapRef.current) {
        try {
          mapRef.current.remove()
        } catch (e) {
          console.log('Map cleanup completed')
        }
      }
    }
  }, [isClient])

  if (!isClient) {
    return (
      <div style={{ 
        height: '500px', 
        backgroundColor: '#f8f9fa', 
        display: 'flex', 
        alignItems: 'center', 
        justifyContent: 'center',
        border: '2px dashed #dee2e6',
        borderRadius: '8px'
      }}>
        <div style={{ textAlign: 'center' }}>
          <div style={{ fontSize: '18px', marginBottom: '8px' }}>üåç</div>
          <div>Preparing map...</div>
        </div>
      </div>
    )
  }

  if (error) {
    return (
      <div style={{ 
        height: '500px', 
        backgroundColor: '#f8d7da', 
        display: 'flex', 
        alignItems: 'center', 
        justifyContent: 'center',
        border: '2px solid #f5c6cb',
        borderRadius: '8px',
        padding: '20px'
      }}>
        <div style={{ textAlign: 'center', color: '#721c24' }}>
          <div style={{ fontSize: '24px', marginBottom: '12px' }}>‚ùå</div>
          <div style={{ fontSize: '18px', marginBottom: '8px' }}>Map Loading Failed</div>
          <div style={{ fontSize: '14px' }}>Error: {error}</div>
          <button 
            onClick={() => window.location.reload()} 
            style={{ 
              marginTop: '12px', 
              padding: '8px 16px', 
              backgroundColor: '#dc3545', 
              color: 'white', 
              border: 'none', 
              borderRadius: '4px',
              cursor: 'pointer'
            }}
          >
            Reload Page
          </button>
        </div>
      </div>
    )
  }

  return (
    <div style={{ position: 'relative' }}>
      <div 
        ref={containerRef}
        style={{ 
          height: '500px', 
          width: '100%',
          backgroundColor: '#e9ecef',
          border: '2px solid #495057',
          borderRadius: '8px'
        }}
      />
      
      {!mapLoaded && (
        <div style={{
          position: 'absolute',
          top: '50%',
          left: '50%',
          transform: 'translate(-50%, -50%)',
          backgroundColor: 'rgba(255,255,255,0.95)',
          padding: '20px',
          borderRadius: '8px',
          textAlign: 'center',
          boxShadow: '0 4px 6px rgba(0,0,0,0.1)'
        }}>
          <div style={{ fontSize: '18px', marginBottom: '8px' }}>üó∫Ô∏è</div>
          <div style={{ fontSize: '16px', marginBottom: '4px' }}>Loading Map</div>
          <div style={{ fontSize: '14px', color: '#666' }}>{loadingStep}</div>
        </div>
      )}
      
      {mapLoaded && (
        <div style={{
          position: 'absolute',
          top: '10px',
          right: '10px',
          backgroundColor: 'rgba(40, 167, 69, 0.9)',
          color: 'white',
          padding: '8px 12px',
          borderRadius: '4px',
          fontSize: '14px'
        }}>
          ‚úÖ Map Ready!
        </div>
      )}
    </div>
  )
}

export default function MapPage() {
  return (
    <div style={{ 
      padding: '20px', 
      minHeight: '100vh',
      backgroundColor: '#ffffff'
    }}>
      <h1 style={{ 
        marginBottom: '20px',
        color: '#333',
        fontSize: '36px'
      }}>
        AI Map Helper
      </h1>
      
      <div style={{ 
        marginBottom: '16px',
        padding: '12px',
        backgroundColor: '#d1ecf1',
        border: '1px solid #bee5eb',
        borderRadius: '4px',
        color: '#0c5460'
      }}>
        <strong>Status:</strong> Testing bulletproof map implementation
      </div>
      
      <BulletproofMap />
    </div>
  )
}

